import { Stack } from "@mui/material"
import { categories } from '../utilities/constans'


const SideBar = ( {selectedCategory, setSelectedCategory} ) => (
    <Stack  direction="row"
    sx={{
      overflowY: "auto",
      height: { sx: "auto", md: "95%" },
      flexDirection: { md: "column" },
    }} >
        {categories.map((category) => (
            <button
            onClick={() => setSelectedCategory(category.name)} className="category-btn" style={{background: category.name === selectedCategory && '#FC1503', color: 'white'}}  key={category.name} >
                <span style={{color: category.name === selectedCategory ? 'white' : 'red'}}></span>
                <span style={{opacity: category.name === selectedCategory ? '1' : '2' }}>{category.name}</span>
            </button>
        ))}
    </Stack>
  )


export default SideBar